a {
  color: inherit;
  font-size: 1.8vw;
  font-weight: bold;
  text-decoration: none;
}

a:hover, a:active {
  color: #3354f8;
}

.back-link {
  color: inherit;
  align-items: center;
  padding-left: 4vw;
  font-weight: bold;
  text-decoration: none;
  display: inline-flex;
  position: relative;
}

.back-link:hover {
  color: #3354f8;
}

.back-link:before {
  content: "←";
  font-size: 3.8vw;
  transition: color .3s;
  animation: 3s infinite moveArrow;
  position: absolute;
  top: -3.2vh;
  left: 0;
}

@keyframes moveArrow {
  0% {
    left: -7vw;
  }

  50% {
    left: 0;
  }

  51% {
    left: 0;
  }

  100% {
    left: -7vw;
  }
}

.timerPosition {
  background-color: #f0f0f0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.date-button-position {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10vw;
  margin-bottom: 5vh;
  display: flex;
}

#datetime-picker {
  border: 1px solid #ccc;
  border-radius: .5vw;
  width: 60vw;
  height: 5vh;
  margin-bottom: 2vh;
  padding: .5vw;
  font-size: 1.5vw;
}

button[type="button"] {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: .5vw;
  width: 20vw;
  height: 5vh;
  margin-bottom: 4vh;
  font-size: 1.5vw;
  transition: background-color .3s;
}

button[type="button"]:hover {
  background-color: #518554;
}

.timer {
  background-color: #fff;
  border-radius: 1vw;
  justify-content: center;
  gap: 2vw;
  padding: 2vw;
  display: flex;
  box-shadow: 0 .5vw 1vw #0000001a;
}

.field {
  flex-direction: column;
  align-items: center;
  font-size: 1.5vw;
  display: flex;
}

.value {
  margin-bottom: .5vh;
  font-size: 3vw;
  font-weight: bold;
}

.label {
  color: #777;
  font-size: 1.2vw;
}

.flatpickr-calendar {
  font-size: auto;
  justify-content: center;
  align-items: center;
  width: auto;
  height: auto;
  display: flex;
}

.flatpickr-time input {
  font-size: auto;
}

.flatpickr-day {
  width: auto;
  height: auto;
}

.flatpickr-month, .flatpickr-weekday, .flatpickr-time {
  font-size: auto;
}
